import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select } from "antd";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class UserCreateForm extends Component {
  state = {
    repeat_password:null
  }

  handleChangeRepeatPassword= e => {
    this.setState({
      repeat_password: null
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if(values.repeat_password != values.password){
        this.setState({
          repeat_password: "Repeat password must be same as the password"
        })
      }
      else if (!err) {
        console.log("やった")
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { user } = this.props;
    const { loading, loadingSubmit } = user;
    // const data = user.user ? user.user : [];

    // const used = data.is_used;
    const error = user.errorField ? user.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="users.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="users.add.username" />}
              validateStatus={
                error.username || getFieldError("username")
                  ? "error"
                  : null
              }
              help={
                error.username
                  ? error.username
                  : getFieldError("username")
              }
            >
              {getFieldDecorator("username", {
                // initialValue: data.user_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                //   disabled={used ? true : false}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="users.add.password" />}

            >

              {getFieldDecorator("password", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    min: 6,
                    message: <IntlMessages id="minLength6" />
                  },
                ]
                // initialValue: data.description
              })(<Input.Password />)}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="users.add.repeatPassword" />}
              validateStatus={
                this.state.repeat_password || getFieldError("repeat_password")
                  ? "error"
                  : null
              }
              help={
                this.state.repeat_password
                  ? this.state.repeat_password
                  : getFieldError("repeat_password")
              }
            >
              {getFieldDecorator("repeat_password", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    min: 6,
                    message: <IntlMessages id="minLength6" />
                  },
                ]
                // initialValue: data.description
              })(<Input.Password 
                  onChange={this.handleChangeRepeatPassword}
              />)}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="users.add.role" />}
            >
              {getFieldDecorator("id_role", {
                //initialValue: data.id_account_adjustment,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  mode="multiple"
                  loading={this.props.user.loadingFilterRoles}
                  showSearch
                  optionFilterProp="children"
                  //onChange={this.handleChangeRole}
                  //disabled={used ? true : false}
                >
                  {this.props.user.roles.map((data, key) => (
                    <Option key={key} value={data.id_role}>
                      {data.role_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedUserCreateForm = Form.create({
  name: "create_user"
})(UserCreateForm);

export default WrappedUserCreateForm;
