import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import UserCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/user/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/users"

class CreateUser extends Component {

  componentDidMount = () => {
    //console.log("このコードは秘密");
    const { createUser } = this.props;
    createUser();
  }

  handleSubmit = value => {
    const { storeUser } = this.props;
    storeUser(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.user.error) {
      notification("error", this.props.user.error);
      this.props.clearError();
    }

    if (this.props.user.success) {
      this.props.history.replace(redirectTo)
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="users.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <UserCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            user={this.props.user}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    user: state.user
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateUser);
