const actions = {
  GET_USERS_REQUEST: "GET_USERS_REQUEST",
  GET_USERS_SUCCESS: "GET_USERS_SUCCESS",
  STORE_USER_REQUEST: "STORE_USER_REQUEST",
  STORE_USER_SUCCESS: "STORE_USER_SUCCESS",
  STORE_USER_ERROR: "STORE_USER_ERROR",
  EDIT_USER_REQUEST: "EDIT_USER_REQUEST",
  EDIT_USER_SUCCESS: "EDIT_USER_SUCCESS",
  EDIT_USER_ERROR: "EDIT_USER_ERROR",
  UPDATE_USER_REQUEST: "UPDATE_USER_REQUEST",
  UPDATE_USER_SUCCESS: "UPDATE_USER_SUCCESS",
  UPDATE_USER_ERROR: "UPDATE_USER_ERROR",
  SHOW_USER_REQUEST: "SHOW_USER_REQUEST",
  SHOW_USER_SUCCESS: "SHOW_USER_SUCCESS",
  SHOW_USER_ERROR: "SHOW_USER_ERROR",
  DEACTIVATE_USER_REQUEST: "DEACTIVATE_USER_REQUEST",
  DEACTIVATE_USER_SUCCESS: "DEACTIVATE_USER_SUCCESS",
  DEACTIVATE_USER_ERROR: "DEACTIVATE_USER_ERROR",
  ACTIVATE_USER_REQUEST: "ACTIVATE_USER_REQUEST",
  ACTIVATE_USER_SUCCESS: "ACTIVATE_USER_SUCCESS",
  ACTIVATE_USER_ERROR: "ACTIVATE_USER_ERROR",
  CLEAR_SUCCESS: "CLEAR_SUCCESS",
  CLEAR_ERROR: "CLEAR_ERROR",
  CLEAR_ERROR_FIELD: "CLEAR_ERROR_FIELD",
  getUsers: (page, search, filter) => ({
    type: "GET_USERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createUser: () => ({
    type:"CREATE_USER_REQUEST"
  }),
  storeUser: data => ({
    type: "STORE_USER_REQUEST",
    payload: { body: data }
  }),
  editUser: id_user => ({
    type: "EDIT_USER_REQUEST",
    payload: { id_user: id_user }
  }),
  updateUser: (id_user, data) => ({
    type: "UPDATE_USER_REQUEST",
    payload: {
      id_user: id_user,
      body: data
    }
  }),
  showUser: id_user => ({
    type: "SHOW_USER_REQUEST",
    payload: { id_user: id_user }
  }),
  deactivateUser: id_user => ({
    type: "DEACTIVATE_USER_REQUEST",
    payload: { id_user }
  }),
  activateUser: id_user => ({
    type: "ACTIVATE_USER_REQUEST",
    payload: { id_user }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
